import React, { Component, useState } from "react";
import { Container, Row, Col, Card } from "react-bootstrap";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

const Homeduan = () => {
  // constructor(props) {
  //   super(props);
  //   this.next = this.next.bind(this);
  //   this.previous = this.previous.bind(this);
  // }
  const [slider, setSlider] = useState(null);
  const next = () => {
    slider.slickNext();
  };
  const previous = () => {
    slider.slickPrev();
  };
  var settings = {
    dots: false,
    infinite: true,
    speed: 500,
    autoplay: true,
    autoplaySpeed: 2000,
    slidesToShow: 1,
    slidesToScroll: 1,
    initialSlide: 0,
    arrows: false,
    // responsive: [
    //   {
    //     breakpoint: 1024,
    //     settings: {
    //       slidesToShow: 4,
    //       slidesToScroll: 1,
    //       infinite: true,
    //       dots: true,
    //     },
    //   },
    //   {
    //     breakpoint: 600,
    //     settings: {
    //       slidesToShow: 2,
    //       slidesToScroll: 1,
    //       initialSlide: 2,
    //     },
    //   },
    //   {
    //     breakpoint: 480,
    //     settings: {
    //       slidesToShow: 1,
    //       slidesToScroll: 1,
    //     },
    //   },
    // ],
  };

  return (
    <Container className="text-center" fluid={true}>
      <div className="section-title text-center mb-55">
        <h2>
          TIN TỨC - SỰ KIỆN&nbsp;
          <a className="btn btn-sm ml-2 site-btn" onClick={previous}>
            <i className="fa fa-angle-left"></i>
          </a>
          &nbsp;
          <a className="btn btn-sm ml-2 site-btn" onClick={next}>
            <i className="fa fa-angle-right"></i>
          </a>
        </h2>
        {/* <p>
          Một số sản phẩm mới được cập nhật vào cửa hàng, có thể bạn sẽ quan tâm
        </p> */}
      </div>

      <Row>
        <div className="sliders">
          <Slider ref={(c) => setSlider(c)} {...settings}>
            <div>
              <Card className="image-box card">
                <img
                  className="center"
                  src="https://www.ptsc.com.vn/Data/Sites/1/News/57/duan-3.jpeg"
                />
                <Card.Body>
                  <p className="product-name-on-card">
                    Realme C21 (Cross Black, 64 GB)
                  </p>
                  <p className="product-price-on-card">Price : $120</p>
                </Card.Body>
              </Card>
            </div>
            <div>
              <Card className="image-box card">
                <img
                  className="center"
                  src="https://www.ptsc.com.vn/Data/Sites/1/News/64/lng-ho%C3%A0n-th%C3%A0nh.jpg"
                />
                <Card.Body>
                  <p className="product-name-on-card">
                    Realme C21 (Cross Black, 64 GB)
                  </p>
                  <p className="product-price-on-card">Price : $120</p>
                </Card.Body>
              </Card>
            </div>
            <div>
              <Card className="image-box card">
                <img
                  className="center"
                  src="https://www.ptsc.com.vn/Data/Sites/1/News/59/duan-2.jpeg"
                />
                <Card.Body>
                  <p className="product-name-on-card">
                    Realme C21 (Cross Black, 64 GB)
                  </p>
                  <p className="product-price-on-card">Price : $120</p>
                </Card.Body>
              </Card>
            </div>
            <div>
              <Card className="image-box card">
                <img
                  className="center"
                  src="https://www.ptsc.com.vn/Data/Sites/1/News/61/duan-6.jpeg"
                />
                <Card.Body>
                  <p className="product-name-on-card">
                    Realme C21 (Cross Black, 64 GB)
                  </p>
                  <p className="product-price-on-card">Price : $120</p>
                </Card.Body>
              </Card>
            </div>
          </Slider>
        </div>
      </Row>
    </Container>
  );
};
export default Homeduan;
